"use client"

import * as React from "react"
import { useRouter, useSearchParams } from "next/navigation"
import { toast } from "sonner"

import { cn } from "@/lib/utils"
import { Button } from "@/components/ui/button"
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from "@/components/ui/card"
import {
  Field,
  FieldDescription,
  FieldGroup,
  FieldLabel,
} from "@/components/ui/field"
import { Input } from "@/components/ui/input"
import { useAuth } from "@/features/auth"

export function LoginForm({
  className,
  ...props
}) {
  const router = useRouter()
  const searchParams = useSearchParams()
  const { loginWithOpenId, isAuthenticated, isReady, user } = useAuth()

  const [email, setEmail] = React.useState("")
  const [password, setPassword] = React.useState("")
  const [isSubmitting, setIsSubmitting] = React.useState(false)

  const redirectTo = searchParams?.get("redirect") || "/"

  React.useEffect(() => {
    if (isReady && isAuthenticated) {
      router.replace(redirectTo)
    }
  }, [isAuthenticated, isReady, redirectTo, router])

  React.useEffect(() => {
    if (!email && user?.email) {
      setEmail(user.email)
    }
  }, [user, email])

  const handleSuccess = React.useCallback((loggedInUser) => {
    if (loggedInUser?.name) {
      toast.success(`환영합니다, ${loggedInUser.name}님!`)
    } else {
      toast.success("로그인에 성공했습니다.")
    }
    router.replace(redirectTo)
  }, [redirectTo, router])

  const authenticate = React.useCallback(async () => {
    setIsSubmitting(true)
    try {
      const session = await loginWithOpenId({
        provider: "dummy-openid",
        email,
        password,
      })
      handleSuccess(session?.user)
    } catch (error) {
      console.error("OpenID 로그인 실패", error)
      toast.error("로그인 처리 중 문제가 발생했습니다. 다시 시도해주세요.")
    } finally {
      setIsSubmitting(false)
    }
  }, [email, password, loginWithOpenId, handleSuccess])

  const onSubmit = React.useCallback((event) => {
    event.preventDefault()
    authenticate()
  }, [authenticate])

  const onSsoClick = React.useCallback(() => {
    authenticate()
  }, [authenticate])

  return (
    <div className={cn("flex flex-col gap-6", className)} {...props}>
      <Card>
        <CardHeader>
          <CardTitle>Login to your account</CardTitle>
          <CardDescription>
            조직의 OpenID 공급자를 통해 안전하게 로그인하세요.
          </CardDescription>
        </CardHeader>
        <CardContent>
          <form onSubmit={onSubmit} className="space-y-4">
            <FieldGroup>
              <Field>
                <FieldLabel htmlFor="email">Work Email</FieldLabel>
                <Input
                  id="email"
                  type="email"
                  placeholder="m@example.com"
                  value={email}
                  onChange={(event) => setEmail(event.target.value)}
                  required
                />
              </Field>
              <Field>
                <div className="flex items-center">
                  <FieldLabel htmlFor="password">Password</FieldLabel>
                  <a
                    href="#"
                    className="ml-auto inline-block text-sm underline-offset-4 hover:underline"
                  >
                    Forgot your password?
                  </a>
                </div>
                <Input
                  id="password"
                  type="password"
                  value={password}
                  onChange={(event) => setPassword(event.target.value)}
                  required
                />
              </Field>
              <Field className="space-y-2">
                <Button type="submit" disabled={isSubmitting}>
                  {isSubmitting ? "Processing..." : "Login"}
                </Button>
                <Button
                  variant="outline"
                  type="button"
                  disabled={isSubmitting}
                  onClick={onSsoClick}
                >
                  {isSubmitting ? "Connecting..." : "Continue with OpenID SSO"}
                </Button>
                <FieldDescription className="text-center">
                  아직 계정이 없으신가요? <a href="#">Sign up</a>
                </FieldDescription>
              </Field>
            </FieldGroup>
          </form>
        </CardContent>
      </Card>
    </div>
  )
}
